@import url(https://fonts.googleapis.com/css?family=Sen&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bgVideo {
    position: fixed;
    left: 0;
    min-height: 20vh;
    overflow: hidden;
    z-index: -1;
}
.homeContainer {
    padding: 20vh 5vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: space-around;
}

.greyContainer {
    min-width: 50vw;
    min-height: 60vh;
    background-color: rgb(0, 0 , 0, 0.5);
    font-family: 'Sen', sans-serif;
    color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 3vh;
}

.fullPic {
    min-height: 20vh;
    max-height: 40vh;
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.des {
    min-width: 50%;
    max-width: 70%;
    display: block;
    padding: 3vw;
    font-size: 3vh;
    color: rgb(255, 255, 255);
    text-align: left;
}

.I1 {
    color: salmon;
}

.I2 {
    color: white;
}

.I3 {
    color: lightgreen;
}

.musicPic {
    height: 50vh;
    width: 50vh;
}

.header {
    font-family: 'Sen', sans-serif;
    color: white;
    font-size: 7vh;
    padding: 2vh;
}

.blueContainer {
    z-index: 0;
    min-width: 70vw;
    min-height: 60vh;
    background-color: rgba(0, 132, 255, 0.25);
    font-family: 'Sen', sans-serif;
    color: white;
    padding: 3vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.car {
    width: 80%;
}

.barBody {
    background-color: rgba(255, 255, 255, 0.75);
    min-height: 7vh;
    padding: 2vh;
    display: flex;
    justify-content: space-around;
}

.barItems {
    width: 60%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.barItems a {
    font-size: 4vh;
    text-decoration: none;
    color: #000;
}
::-webkit-scrollbar {
  display: none;
}

.App {
  text-align: center;
}

